import React from 'react'

export default function Services() {
  return (
    <section className="page-section py-5" id="services" name="services">
      <div className="container">
        <div className="text-center">
          <h1 className="section-heading text-uppercase " >Services</h1>
        </div>
        <div className="row text-center">
          <div className="col-md-4">
            <span className="fa-stack fa-4x">
              <i className="fas fa-circle fa-stack-2x" style={{ color: '#fed136' }}></i>
              <i className="fa fa-file-code  fa-stack-1x fa-inverse"></i>
            </span>
            <h4 className="my-3">Web Application</h4>
            <p className="text-muted">Developing web-application that interacts with the web browser by dynamically rewriting the current web page with new data from the web server</p>
          </div>
          <div className="col-md-4">
            <span className="pt-3" style={{ backgroundColor: '#fed136', borderRadius: '50%', width: '130px', height: '130px', display: 'inline-block', color: '#fff', lineHeight: '130px' }}>
              <i className="fas fa-laptop fa-3x"> </i>
              <i className="fa fa-plus fa-2x" aria-hidden="true"> </i>
              <i className="fa fa-mobile fa-3x " aria-hidden="true"> </i>
            </span>

            <h4 className="my-3">Responsive Design</h4>
            <p className="text-muted">On responsive websites, the same information and page elements appear no matter what device you’re on.</p>
          </div>
          <div className="col-md-4">
            <span className="fa-stack fa-4x">
              <i className="fas fa-circle fa-stack-2x" style={{ color: '#fed136' }}></i>
              <i className="fas fa-search fa-stack-1x fa-inverse"></i>
            </span>
            <h4 className="my-3">On Page SEO</h4>
            <p className="text-muted">No matter if you have a beautifully rendered website, or a stunning portfolio you won’t get very far if you’re lacking sufficient SEO (Search Engine Optimisation).</p>
          </div>
        </div>
      </div>
    </section>
  )
}

