import React, { useState, useEffect } from 'react'
import myPhoto from '../assets/images/me/me.jpg'
import { Collapse, ListGroup, ListGroupItem } from 'reactstrap';

export default function About() {

  let buttons = [];
  const [isOpen1, setIsOpen1] = useState(false);
  const [isOpen2, setIsOpen2] = useState(false);
  const [isOpen3, setIsOpen3] = useState(false);
  const [isOpen4, setIsOpen4] = useState(false);

  useEffect(() => {
    buttons = Array.from(document.querySelectorAll('.skillBtn'));
  }, [])

  const toggle1 = (e) => {
    setIsOpen1(!isOpen1);
    handleClick(e);
  }
  const toggle2 = (e) => {
    setIsOpen2(!isOpen2);
    handleClick(e);
  }
  const toggle3 = (e) => {
    setIsOpen3(!isOpen3);
    handleClick(e);
  }
  const toggle4 = (e) => {
    setIsOpen4(!isOpen4);
    handleClick(e);
  }

  const handleClick = (e) => {
    e.target.classList.toggle('active');
  }

  return (
    <section className="page-section py-5" id="about" name="about">
      <div className="container">

        <div className="text-center">
          <h1 className="section-heading text-uppercase " >About</h1>
        </div>

        <div className="row">

          <div className="col-md-6">

            <div className="my-photo text-center" >
              <img src={myPhoto} alt="My profile" width="50%" height="auto" />
            </div>

            <div className="summary">
              <p>
                My name is<strong> Ahmed Tarek</strong>, I am passionate Full Stack Developer with the focus on MERN-Stack, I have multiple years of experience in the field of software development (since 2017), I have built many applications in both server-side and client-side.
                </p>
              <p>
                I also come from three different career backgrounds: Web Development, Embedded Systems, and Machine Learning. Embedded Systems was my first speciality at university and I did several projects based on AVR architecture and Arduino, then went to Machine Learning Specialization as it is part of my Master degree study at Cairo University.
                </p>
            </div>

          </div>

          <div className="col-md-6">

            <div className="text-left">
              <h2>- SKILLS</h2>
            </div>


            <button className="skillBtn btn btn-secondary" id="toggler1" onClick={toggle1}>
              Web Markup Languages
            </button>
            <Collapse isOpen={isOpen1} toggler="#toggler1">
              <ListGroup>
                <ListGroupItem>Javascript</ListGroupItem>
                <ListGroupItem>HTML 5</ListGroupItem>
                <ListGroupItem>CSS 3</ListGroupItem>
                <ListGroupItem>Bootstrap 4</ListGroupItem>
                <ListGroupItem>SASS</ListGroupItem>
                <ListGroupItem>JSX</ListGroupItem>
              </ListGroup>
            </Collapse>

            <br />

            <button className="skillBtn btn btn-secondary" id="toggler2" onClick={toggle2}>
              Javascript frame work: React JS
            </button>
            <Collapse isOpen={isOpen2} toggler="#toggler2">
              <ListGroup>
                <ListGroupItem>React Redux</ListGroupItem>
                <ListGroupItem>React Router</ListGroupItem>
                <ListGroupItem>React Hooks</ListGroupItem>
                <ListGroupItem>HTTP/AJAX and Axios</ListGroupItem>
                <ListGroupItem>Styled Component</ListGroupItem>
              </ListGroup>
            </Collapse>


            <br />


            <button className="skillBtn btn btn-secondary" id="toggler3" onClick={toggle3}>
              Server-side language: Node JS
            </button>
            <Collapse isOpen={isOpen3} toggler="#toggler3">
              <ListGroup>
                <ListGroupItem>Express JS</ListGroupItem>
                <ListGroupItem>REST APIs</ListGroupItem>
                <ListGroupItem>Mongoose</ListGroupItem>
                <ListGroupItem>JWT Authentication</ListGroupItem>
                <ListGroupItem>Multer v3</ListGroupItem>
                <ListGroupItem>Socket.io</ListGroupItem>
              </ListGroup>
            </Collapse>


            <br />


            <button className="skillBtn btn btn-secondary" id="toggler4" onClick={toggle4}>
              Databases
            </button>
            <Collapse isOpen={isOpen4} toggler="#toggler4">
              <ListGroup>
                <ListGroupItem>No-SQL</ListGroupItem>
                <ListGroupItem>Mongo DB</ListGroupItem>
              </ListGroup>
            </Collapse>



          </div>

        </div>
      </div>
    </section >
  )
}
