import React from 'react'
import ProjectModal from './ProjectModal'
import {
  Card, CardImg, CardText, CardBody,
  CardTitle, CardSubtitle
} from 'reactstrap';

import amazonaCover from '../assets/images/portfolio/amazona/0.PNG'

export default function Portfolio() {
  return (
    <section className="page-section py-5" id="portfolio" name="portfolio">
      <div className="container">

        <div className="text-center">
          <h1 className="section-heading text-uppercase " >Portfolio</h1>
        </div>

        <div className="row" >
          <Card className="col-md mx-2">
            <a href="https://amazonatemplate.herokuapp.com/" title="see live preview" target="_blank" rel="noopener noreferrer">
              <CardImg top src={amazonaCover} alt="amazona website" />
            </a>
            <CardBody>
              <a href="https://amazonatemplate.herokuapp.com/" title="see live preview" target="_blank" rel="noopener noreferrer">
                <CardTitle>amazona website</CardTitle>
              </a>
              <CardSubtitle>eCommerce wep application</CardSubtitle>
              <CardText>A fully responsive eCommerce website inspired by amazon.com</CardText>

              <ProjectModal />
            </CardBody>
          </Card>

          <Card className="col-md mx-2">
            <a href="https://amazonatemplate.herokuapp.com/" title="see live preview" target="_blank" rel="noopener noreferrer">
              <CardImg top src={amazonaCover} alt="amazona website" />
            </a>
            <CardBody>
              <a href="https://amazonatemplate.herokuapp.com/" title="see live preview" target="_blank" rel="noopener noreferrer">
                <CardTitle>amazona website</CardTitle>
              </a>
              <CardSubtitle>eCommerce wep application</CardSubtitle>
              <CardText>A fully responsive eCommerce website inspired by amazon.com</CardText>

              <ProjectModal />
            </CardBody>
          </Card>

        </div>
      </div>
    </section>
  )
}
